:root {
  --content-width: 1180px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --font-family: "FiraSans", sans-serif;
  --font-size: 20px;
  --accent-color: #dba069;
  --black-color: #333;
  --block-color: rgba(132, 34, 49, 0.05);
  --red-color: #842231;
  --white-color: #fff;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

@font-face {
  font-family: "FiraSans";
  src: url("../fonts/FiraSans-Bold.woff2") format("woff2"), url("../FiraSans-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FiraSans";
  src: url("../fonts/FiraSans-Light.woff2") format("woff2"), url("../FiraSans-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FiraSans";
  src: url("../fonts/FiraSans-Regular.woff2") format("woff2"), url("../FiraSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "FiraSans";
  src: url("../fonts/FiraSans-Medium.woff2") format("woff2"), url("../FiraSans-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothamPro-Bold.woff2") format("woff2"), url("../GothamPro-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "GothamPro";
  src: url("../fonts/GothamPro-Medium.woff2") format("woff2"), url("../GothamPro-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
  object-fit: cover;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

a {
  text-decoration-color: transparent;
  color: inherit;
}

address {
  font-style: normal;
}

html,
body {
  height: 100%;
}

body {
  background-color: var(--white-color);
  color: var(--black-color);
  display: flex;
  flex-direction: column;
  font-family: var(--font-family);
  font-size: var(--font-size);
  font-weight: 300;
  line-height: 1.1;
}

.lock {
  overflow: hidden;
  touch-action: none;
}

.page {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
}

.logo__img {
  object-fit: contain;
}

.sr-only {
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  position: absolute;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  width: 1px;
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
  z-index: 2;
}
@media only screen and (max-width: 767px) {
  .burger {
    display: block;
  }
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--white-color);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}

.phone-link {
  display: flex;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  align-items: center;
  padding: 10px 20px;
}
.phone-link svg {
  margin-right: 10px;
}

.whatsapp-link {
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .whatsapp-link:hover {
    color: var(--accent-color);
  }
}

.phone-link {
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.phone-link::before {
  content: "";
  position: absolute;
  background-color: var(--accent-color);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: translateX(-100%);
  transform-origin: 0 50%;
  transition: transform 0.3s ease 0s;
  z-index: -1;
}
@media (any-hover: hover) {
  .phone-link:hover::before {
    transform: translateX(0);
  }
}

.header {
  min-height: 127px;
  background-color: #000;
}
@media only screen and (max-width: 767px) {
  .header {
    min-height: auto;
  }
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  flex-shrink: 0;
  object-fit: contain;
  transition: opacity 0.3s ease 0s;
  margin-right: 20px;
  z-index: 2;
}
@media only screen and (max-width: 767px) {
  .header__logo .logo {
    height: 80px;
  }
}
@media (any-hover: hover) {
  .header__logo:hover {
    opacity: 0.7;
  }
}
.header__inner {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 767px) {
  .header__inner {
    flex-direction: row;
  }
}
.header__top {
  align-self: flex-end;
  margin-bottom: 15px;
  margin-left: auto;
}
@media only screen and (max-width: 767px) {
  .header__top {
    margin-right: 45px;
    margin-bottom: 0;
  }
}
.header__links {
  display: flex;
  align-items: center;
}

.links__item:not(:last-child) {
  margin-right: 35px;
}
@media only screen and (max-width: 767px) {
  .links__item:not(:last-child) {
    margin-right: 20px;
  }
}
@media only screen and (max-width: 559px) {
  .links__item {
    display: none;
  }
}
.links__link {
  color: var(--white-color);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 400;
}
.links__link svg {
  width: 25px;
  height: 25px;
  fill: currentColor;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media only screen and (max-width: 767px) {
  .menu {
    position: absolute;
    background-color: var(--accent-color);
    left: 0;
    top: 0;
    right: 0;
    flex-direction: column;
    padding: 130px 0 80px;
    justify-content: start;
    transform: translateX(-200%);
    transition: transform 0.4s;
    z-index: 1;
    overflow: auto;
  }
}
@media only screen and (max-width: 767px) {
  .menu__item {
    transform: translateX(-200%);
  }
  .menu__item:nth-child(1) {
    transition: transform 0.6s;
  }
  .menu__item:nth-child(2) {
    transition: transform 0.9s;
  }
  .menu__item:nth-child(3) {
    transition: transform 1.1s;
  }
  .menu__item:nth-child(4) {
    transition: transform 1.3s;
  }
  .menu__item:nth-child(5) {
    transition: transform 1.5s;
  }
  .menu__item:nth-child(6) {
    transition: transform 1.7s;
  }
}
.menu__item:not(:last-child) {
  margin-right: 50px;
}
@media only screen and (max-width: 767px) {
  .menu__item:not(:last-child) {
    margin-right: 0;
  }
}
.menu--active {
  transform: translateX(0);
  overflow-x: hidden;
}
.menu--active .menu__item {
  transform: translateX(0);
}
.menu__link {
  color: var(--white-color);
  display: block;
  font-size: 16px;
  font-weight: 400;
  position: relative;
  padding-bottom: 7px;
  overflow: hidden;
  line-height: 1.5;
}
@media only screen and (max-width: 767px) {
  .menu__link {
    font-size: 24px;
    padding-left: 50px;
  }
}
.menu__link::before {
  content: "";
  position: absolute;
  background-color: var(--accent-color);
  display: block;
  height: 4px;
  width: 100%;
  left: 0;
  bottom: 0;
  transform: translateX(-101%);
  transform-origin: 0 50%;
  transition: transform 0.3s ease 0s;
}
@media (any-hover: hover) {
  .menu__link:hover::before {
    transform: translateX(0);
  }
}

.footer {
  padding: 70px 0;
}
@media only screen and (max-width: 991px) {
  .footer {
    padding: 50px 0;
  }
}
@media only screen and (max-width: 559px) {
  .footer {
    padding: 30px 0;
  }
}
.footer__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.footer__item {
  margin: 0 30px;
}
.footer__link {
  line-height: 1.5;
  transition: color 0.3s ease 0s;
}
@media only screen and (max-width: 559px) {
  .footer__link {
    font-size: 18px;
  }
}
@media (any-hover: hover) {
  .footer__link:hover {
    color: var(--accent-color);
  }
}